.banner{
  background-color: rgba(0, 0, 0, 0.492);
  background: 
  linear-gradient(180deg, rgba(0, 0, 0, 0.568), rgba(0, 0, 0, 0.568)),
  url("../../images/Solar 3.png");
background-size: cover;
background-position: center;
background-repeat: no-repeat;
  text-align: left;
  padding-top:5rem ;
  display: flex;
  align-items: center;
  justify-content: center;
margin:6.6rem 2rem  2rem 2rem;
border-radius: 30px;
}
.banner-content{
  padding: 40px 40px 40px 60px;
  color: white;
  border-radius: 10px;
  text-align: center;
}
.banner-content>button{
  background-color: #2E7D32;
  border: none;
  padding: 15px 20px;
  border-radius: 10px;
  color: #fff;
  font-weight: 600;
} 
.banner-row{
  height: 80vh;
}
.banner-bottom-box {
  background-color: #2E7D32;
  color:white;
  padding: 30px;
  border-top-left-radius: 20%;
  border-bottom-right-radius: 20%;
  text-align: center;
  overflow: hidden;
  transform: scale(1);
  transition: all 0.5s ease-in;
}
.banner-bottom-box:hover{
  transform: scale(0.9);
}

.banner-bottom-box-icon {
  transition: transform 1s ease-in;
  display: inline-block;
  font-size: 3.5rem;
}





.calculator{
  text-align: center;
}
.calculator h2{
  font-size: 2.5rem;
  color: #2E7D32; /* Primary color for heading */
  font-weight: bold;
  margin-bottom: 1rem;

}
.saving-calculater{
  margin-top: 3rem;
}
.calculate-box-1{
  margin-top: 2rem;
  background-color: rgb(90, 90, 207);
  color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 0px 3px 3px rgba(18, 18, 68, 0.508);
  text-align: left;

}
.calculate-box-1>form{
  display: flex;
  flex-direction: column;
}
.calculate-box-1>form>input{
  padding: 10px;
}
.calculate-box-1>form>select{
  padding: 10px;
}
.calculate-box-2{
  margin-top: 2rem;
  display: grid;
  padding: 20px;
  grid-template-columns: repeat(auto-fit,minmax(200px,1fr));
  gap: 30px;
}

.calculate-box{
  background-color: rgb(90, 90, 207);
  box-shadow: 0px 0px 3px 3px rgba(18, 18, 68, 0.508);

  color: #fff;
  padding: 10px;
  font-size: 18px;
  font-weight: 700;
  border-radius: 10px;
}
.add-btn{
  margin-top: 30px;
  white-space: nowrap;
}
/* Main container styling */
.why-choose-us {
  padding: 3rem 0;
  text-align: center;
  background-color: #f9f9f9; /* Light background color */
}

.why-choose-us h2 {
  font-size: 2.5rem;
  color: #2E7D32; /* Primary color for heading */
  font-weight: bold;
  margin-bottom: 1rem;
}

.why-choose-us p {
  max-width: 600px;
  margin: 0 auto 2rem;
  color: #666;
  font-size: 1rem;
  line-height: 1.6;
}

.why-choose-us .row {
  row-gap: 2rem; 
}

.wcu-box {
  background-color: #fff;
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 4px 15px #8fcd9249;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
  height: 100%;

}

.wcu-box:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.wcu-box img {
  width: 80px;
  height: 80px;
  margin-bottom: 1rem;
  transition: transform 0.3s ease;
}

.wcu-box:hover img {
  transform: scale(1.1);
}

.wcu-box h3 {
  font-size: 1.5rem;
  color: #2E7D32;
  margin: 1rem 0;
  font-weight: 600;
}

.wcu-box p {
  color: #666;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* Container styling for better layout */
.segments {
  padding: 3rem 0;
  text-align: center;
}

.segments h2 {
  font-size: 2.5rem;
  color: #2E7D32; /* Primary color for heading */
  font-weight: bold;
  margin-bottom: 1rem;
}

.segment {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  margin-bottom: 1rem;
}

.segment img {
  width: 100%;
  height: 300px;
  transition: transform 0.3s ease;
  border-radius: 12px;
}

.segment:hover {
  transform: scale(1.03);
}

.segment:hover img {
  transform: scale(1.1);
}

.se-name {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  background-color: rgba(0, 0, 0, 0.6); /* Semi-transparent black overlay */
  color: #fff;
  opacity: 1;
  transition: opacity 0.3s ease;
  border-radius: 12px;
  text-align: left;
  padding: 30px;
}
.segment:hover .se-name{
  background-color: rgba(90, 90, 207, 0.181);
}
.se-name-con{
  opacity: 0;
  height: 0;
  transition: 0.5s all ease-in-out;
}

.segment:hover .se-name-con{
  opacity: 1;
  height: 50px;
}

.se-name h3 {
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  padding: 1rem;
}
.segment-icon{
  font-size: 5rem;
  background-color: #2E7D32;
  padding: 10px;
  border-radius: 50%;

}
.segment:hover .segment-icon{
  background-color: #FF7043;
  animation-name: rotate-icon;
  animation-duration: 0.5s;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;

}
@keyframes rotate-icon {
  0%{
    transform: rotate(0deg);

  }
  25%{
    transform: rotatex(90deg) ;
  }
  75%{
    transform: rotatex(180deg) ;
  }
  100%{
    transform: rotatex(360deg) ;
  }
}

.review{
  margin-top: 2rem;
  background-color:  #fff;
  padding: 40px 20px;
  text-align: center;
}
.review-box{
  box-shadow: 0 0 10px 10px rgba(128, 128, 128, 0.145);
  padding: 24px 24px;
  height: 250px;
  border-radius: 10px;
  margin: 20px 0;
  transition: .3s all ease-in;
}
.review-box:hover{
  color: #fff;
  background-color: rgb(90, 90, 207);
  transform: translatey(-5%);
} 
.review-box img{
  width: 70px;
  height: 70px;
  border-radius: 50%;


}
.review-box-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}
.review-box-top-1{
  display: flex;
  gap: 20px;
}

.review-box-top-2 {
  transform: rotate(180deg);
}
.review-box:hover .review-box-top-2 {
  transform: rotate(0deg);
  filter: invert(0%) sepia(0%) saturate(0%) hue-rotate(0deg) brightness(0%) contrast(100%);
}
.review-box-top-content{
  text-align: left;
}
.review-box-top-content p:first-child{
  margin:5px 0;
  font-size: 18px;
  font-weight: 700;
}
.review-box-content{
  text-align: justify;
  font-size: 14px;
}

.faq-container {
  padding: 40px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

.faq-container h2 {
  text-align: center;
  color: #2E7D32; /* Primary color */
}

.faq-list {
  margin-top: 1.5rem;
}

.faq-item {
  margin-bottom: 1rem;
  padding: 15px 20px;
  background-color: #FAFAFA; /* Background color */
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.faq-item:hover {
  background-color: #F1F8E9; /* Lightened background color on hover */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #333;
}

.faq-question h3 {
  margin: 0;
  font-size: 1.1rem;
}

.faq-question span {
  font-size: 1.5rem;
  color: #FF7043; /* Accent color */
  transition: transform 0.3s;
}

.faq-answer {
  margin-top: 10px;
  color: #000000;
  font-size: 1rem;
  line-height: 1.6;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease, max-height 0.3s ease;
}

/* Open state for answer */
.faq-item.open .faq-answer {
  opacity: 1;
  max-height: 200px;
}

.faq-item.open .faq-question span {
  transform: rotate(45deg); /* Rotate the plus to a cross */
}

.work-with-us{
  background-color: #2E7D32;
}

.solar-steps {
  padding: 40px 20px;
  background-color: #2E7D32;
  position: relative;
  background-image: radial-gradient(circle, #FAFAFA 1px, transparent 1px);
  background-size: 20px 20px;
 
}

.solar-steps h2 {
  color:rgb(255, 255, 255);
  font-size: 32px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2rem;
}

.step-box {
  background-color: #fff!important;
  height: 300px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-bottom: 2rem;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  text-align: center;


}

.step-box:hover {
  transform: translateY(-5px);
}

/* Arrow connection between boxes */
.step-box:not(:last-child)::after {
  content: '';
  position: absolute;
  right: -30px;
  top: 50%;
  width: 60px; /* Length of arrow line */
  height: 2px; /* Thickness of arrow line */
  background: #fff;
  transform: translateY(-50%);
}

.step-box:not(:last-child)::before {
  content: '';
  position: absolute;
  right: -35px; /* Adjusts arrowhead position */
  top: 50%;
  transform: translateY(-50%);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 8px solid #fff;
}
.step-icon{
  height: 70px;
}

/* Responsive adjustments */
@media (min-width:1440px){
  .review-box{
    height: 230px;
  }
}
@media (max-width:1140px) {
  .review-box{
    height: auto;
  }
  .banner-bottom-box {
    border-top-left-radius: 10%;
  border-bottom-right-radius: 10%;
  box-sizing: content-box;
  }
}
@media (max-width: 768px) {
  .solar-steps h2 {
    font-size: 24px;
  }
  
  .step-box {
    height: auto;
    padding: 15px;
    margin-bottom: 1rem;
  }

  .step-box::after,
  .step-box::before {
    display: none;
  }
}


@media (max-width:650px) {
  .box-1,.box-2,.box-3{
 
    margin: 10px;

  }
  .review-box{
    height: auto;
  }
}
@media (max-width:450px) {
  .banner{

    text-align: center;

  }
  .banner-content{
    padding: 0 10px;
  }
  .box-1,.box-2,.box-3{
    position: static;
    margin: 10px;

  }
  .review-box{
    height: auto;
  }

}